import Vue from 'vue'
import Router from 'vue-router'
import firstPage from '@/components/firstPage'
import dataSearch from '@/components/dataSearch'
import home from '@/components/home'
import loginPage from '@/components/loginPage'
import adminLogin from '@/components/admin_login_page'
import admin from '@/components/admin_page'
import refresh from '@/components/refresh'

Vue.use(Router)

export default new Router({
  routes: [{
    path: "/",
    redirect: "/firstPage",
  }, {

    path: '/loginPage',
    name: 'loginPage',
    component: loginPage

  },
    {
      path: '/refresh',
      name: 'refresh',
      component: refresh
    },
    {
      path: "/adminLogin",
      name: 'adminLogin',
      component: adminLogin,
    },
    {
      path: "/admin_page",
      name: 'admin_page',
      component: admin,
    },

    {
      path: '/',
      name: 'home',
      component: home,
      children: [
        {
          path: '/firstPage',
          name: 'firstPage',
          component: firstPage
        },
        {
          path: '/dataSearch',
          name: 'dataSearch',
          component: dataSearch
        },
      ],
    }
  ]
})



