<template>
  <div class="loginPage">
    <div>
      <el-card class="loginCard">
        <div slot="header" class="clearfix">
            <span>登陆界面</span>
          </div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-row>
          <el-form-item label="用户名" prop="age">
            <el-input type="text" v-model="ruleForm.age"></el-input>
          </el-form-item>
          </el-row>
          <el-row>
          <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          </el-row>
          <el-form-item>
            <el-button type="primary" @click="login">登陆</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>
<script>
import Request from "../utils/axiosUtils";
  export default {
    data() {
      var checkAge = (rule, value, callback) => {
        if (value==='') {
          return callback(new Error('请输入用户名'));
        }
//        else
//        {
//          if(this.ruleForm.age!=='a')
//          {  callback(new Error('error not a'))}
//          callback();
//        }
        
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } 
        // else {
        //   if (this.ruleForm.pass !== '123') {
        //     callback(new Error('密码错误'))
        //   }
        //   callback();
        // }
      };
      return {
        ruleForm: {
          pass: '',
          age: ''
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          age: [
            { validator: checkAge, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      login() {
        // this.$refs.ruleFormRef.validate((valid) => {
        //   if (!valid) {
        //     console.log('error submit!!');
        //     return false;
        //   } 
          
        // });
        Request.post("/api/login",{"name":this.ruleForm.age,"password":this.ruleForm.pass}).then((res)=>{
            console.log(res)
            if(res.data.resCode!=0) return this.$message.error('登陆失败');
            this.$message.success('登陆成功');
            //windows.sessionStorage.setItem('token',res.data.token);
            this.$router.push('/firstPage');
            
          });
        
      },
      resetForm() {
        this.ruleForm.age='';
        this.ruleForm.pass='';
        //this.$refs.ruleFormRef.resetFields();
      }
    }
  }

</script>

<style scoped>
.loginPage {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(../assets/login-bg.jpg);
  background-size: 100%;
}

.loginCard {
  background: transparent;
  position: absolute;
  left: 50%;
  top: 50%;


  width: 350px;
  margin: -190px 0 0 -175px;
  border-radius: 5px;
  background: hsla(0, 0%, 100%, 0.3);
  overflow: hidden;
}
</style>


