<template>
  <div class="loginPage">
    <el-card class="loginCard">
      <div slot="header" class="clearfix">
        <span>管理员登陆</span>
      </div>
      <el-form ref="form" :model="form">
        <el-row>
          <el-form-item label="用户名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
        </el-row>
        <el-form-item label="密码">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" round style="width: 100%" @click="clickLogin">登录</el-button>
    </el-card>
  </div>

</template>

<script>
import Request from "../utils/axiosUtils";

export default {
  name: "adminLogin",
  data() {
    return {
      form: {
        name: '',
        password: ''
      }
    }
  },
  methods: {
    clickLogin() {
      Request.post("/api/adminLogin", {
        "adminAccount": this.form.name,
        "adminPassword": this.form.password,
      }).then((res) => {
        if (res.data.resCode != 0) {
          this.$message.error('账号或密码错误');

        } else {
          this.$cookies.set("adminUuid", res.data.message.userId)
          this.$router.push('/admin_page');
        }
      });

    }

  }
}
</script>

<style scoped>
.loginPage {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(../assets/login-bg.jpg);
  background-size: 100%;
}

.loginCard {
  background: transparent;
  position: absolute;
  left: 50%;
  top: 50%;


  width: 350px;
  margin: -190px 0 0 -175px;
  border-radius: 5px;
  background: hsla(0, 0%, 100%, 0.3);
  overflow: hidden;
}
</style>



