export default class CommonConstants {

  static get authorityMap() {
    const myMap = new Map();

    myMap.set("管理员", 99);
    myMap.set("普通用户", 98);
    return myMap;
  }

  static get authorityResMap() {
    const myMap = new Map();

    myMap.set(99, "管理员");
    myMap.set(98, "普通用户");
    return myMap;
  }

}



