<template>
  <el-card>
    <div slot="header" class="clearfix">
      <el-row>
        <el-col :span="10" :offset="4">
          <div class="block">
            <span class="demonstration">日期:</span>
            <el-date-picker
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="clickSearch">检索</el-button>
        </el-col>
        <el-col :span="2" :offset="6">
          <el-button type="primary" @click="clickExport">导出</el-button>
        </el-col>


        <el-col :span="2" :offset="6">
          <el-button type="primary" @click="clickInsert">插入</el-button>
        </el-col>
      </el-row>
    </div>

    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="date"
        label="日期"
      >
      </el-table-column>
      <el-table-column
        prop="name"
        label="炉号"
      >
      </el-table-column>
      <el-table-column
        prop="address"
        label="温度">
      </el-table-column>
      <el-table-column
        prop="flag"
        label="缺铝报警">
      </el-table-column>
    </el-table>
  </el-card>
</template>

<script>
import Request from "../utils/axiosUtils";
import axios from 'axios';
export default {

  name: "dataSearch",
  data() {
    return {
      timer:"",
      value1: "",
      startDate:"",
      endData:"",
      tableData: [
      // {
      //   date: '2016-05-02',
      //   name: '01',
      //   address: '650',
      //   flag: 0
      // }, {
      //   date: '2016-05-04',
      //   name: '02',
      //   address: '600',
      //   flag: 0
      // }, {
      //   date: '2016-05-01',
      //   name: '03',
      //   address: '700',
      //   flag: 0
      // }, {
      //   date: '2016-05-03',
      //   name: '04',
      //   address: '900',
      //   flag: 0
      // }
      ]
    }
  },
  methods:{
    
    clickSearch(){
      if(this.value1=="") return;
      this.startDate= this.value1[0];
      this.endData  = this.value1[1];
      Request.post("/api/searchLog",{"startDate":this.startDate,"endDate":this.endData}).then((res)=>{
        console.log(res);
        const mesArray=res.data.message.map(({timestamp:date, furnaceId:name, currentTemperature:address,status:flag})=>
         ({date,name,address,flag})
       );
      
      this.tableData=mesArray;
      }
      )
    },

    clickInsert(){
      Request.post("/api/collectHoldingFurnace",{"holdingFurnaceId":"ss7","status":1,"currentTemperature":800}).then((res)=>{
        console.log(res);
      })
    },

    clickExport(){
      if(this.value1=="") return;
      Request.post("/api/saveExcel",{"startDate":this.startDate,"endDate":this.endData}).then((res)=>{
        console.log(res);
        if(res.data.message!="")
        {
          if (this.timer) {
            clearInterval(this.timer)
          } else {
            this.timer = setInterval(() => {
              let downloadId = res.data.message;
              Request.post("/api/checkDownloadId",{"downloadId":downloadId}).then((res)=>{
                if(res.data.message==1)
                {
                 
                  axios.get("/api/downloadExcel?downloadId="+downloadId,
                  {responseType: 'blob'}).then((res)=>{
                    console.log(res);
                    if (!res) {
                      return
                    }
                    
                    let url = window.URL.createObjectURL(res.data)
                    let link = document.createElement('a')
                    //link.style.display = 'none'
                    link.href = url
                    link.setAttribute('download', 'download.xls')
                    //link.download = "download.xls";
                    document.body.appendChild(link)
                    link.click()

                    // const a = document.createElement('a');
                    // const url = URL.createObjectURL(res.data);
                    // a.href = url;
                    // a.download = "文件名.xls";
                    // document.body.appendChild(a);
                    // a.click();
                    // document.body.removeChild(a);
                    // URL.revokeObjectURL(url);
                    // let decodedFileName = '1.xls';

                    // if (window.navigator && window.navigator.msSaveOrOpenBlob) {
                    //     window.navigator.msSaveOrOpenBlob(res.data, decodedFileName);
                    // } else {
                    //     const a = document.createElement('a');
                    //     const url = URL.createObjectURL(res.data);
                    //     a.href = url;
                    //     a.download = decodedFileName;
                    //     document.body.appendChild(a);
                    //     a.click();
                    //     document.body.removeChild(a);
                    //     URL.revokeObjectURL(url);
                    // }
                  }).catch((res)=>{
                    //
                  })


                  // window.open("/api/downloadExcel?downloadId="+downloadId);
                  // Request.get("/api/downloadExcel?downloadId="+downloadId).then((res)=>{
                  //   console.log(res)
                  // });
                  clearInterval(this.timer);
                }
              })
              
            }, 1500);
          }
        }
      })
     
    },

  }
}
</script>

<style scoped>

</style>



