<template>
  <div>
    <el-card class="loginCard">
      <div slot="header" class="clearfix">
        <span>添加用户</span>
        <el-button type="primary" icon="el-icon-plus" circle @click="addUserButtonClick"></el-button>


      </div>
      <el-table
        :data="tableData"
        border
        :header-cell-style="{'text-align':'center'}"
        style="width: 100%;">

        <!--      <el-table-column-->
        <!--        key="3"-->
        <!--        fixed-->
        <!--        prop="date1"-->
        <!--        label="最后修改日期">-->
        <!--      </el-table-column>-->
        <el-table-column
          key="1"
          prop="name"
          label="用户名">
        </el-table-column>
        <el-table-column
          key="2"
          prop="password"
          label="密码">
        </el-table-column>
        <el-table-column
          key="5"
          prop="authority"
          label="当前权限">
        </el-table-column>
        <el-table-column
          key="3"
          prop="timestamp"
          label="最后修改时间">
        </el-table-column>

        <el-table-column
          key="4"
          fixed="right"
          label="操作">
          <template slot-scope="scope">
            <!--            <el-button @click="handleClick(scope.row)" type="primary" size="small">查看</el-button>-->
            <el-button type="danger" round size="small" @click="deleteUser(scope.$index)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-dialog
      title="添加新用户"
      :visible.sync="addUserDialogVisible"
      width="30%"
    >
      <el-form ref="form" :model="form">
        <el-row>
          <el-form-item label="用户名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
        </el-row>
        <el-form-item label="密码">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="权限">
          <el-select v-model="authorityValue" placeholder="请选择">
            <el-option
              v-for="item in authorityOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addUserDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="clickAddUsers">确 定</el-button>
  </span>
    </el-dialog>

    <el-dialog
      title="提示"
      :visible.sync="deleteDialog.deletDialogVisible"
      width="30%">
      <span class="deleText">删除用户{{ this.deleteDialog.user }}?</span>
      <div class="dialog-footer">
        <el-button @click="deleteDialog.deletDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmDeleteUser">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Request from "../utils/axiosUtils";
import CommonConstants from "../constants/commom_constants"

export default {
  name: "admin",
  data() {
    return {
      form: {
        name: '',
        password: ''
      },
      tableData: [],
      addUserDialogVisible: false,
      deleteDialog: {
        deletDialogVisible: false,
        user: "",
        index: -1

      },
      authorityOptions: [{
        value: '普通用户',
        label: '普通用户'
      }, {
        value: '管理员',
        label: '管理员'
      },],
      authorityValue: "管理员"
    }
  },
  mounted() {
    const adminUuid = this.$cookies.get("adminUuid")
    if (adminUuid == undefined) {
      this.$router.push('/adminLogin');

    } else {
      Request.post("/api/getAllUser", {
        "adminUserID": adminUuid,
      }).then((res) => {
        console.log(res);
        if (res.data.resCode != 0) {
          this.$message.error('账号或密码错误');

        } else {
          const mesArray = res.data.message.map(({userAccount: name, userPassword: password, userAuthority: authority, timestamp: timestamp, userId: userId}) => {
              authority = CommonConstants.authorityResMap.get(authority);
              return {name, password, authority, timestamp, userId}
            }
          );

          this.tableData = mesArray;
        }
      });
    }
  },
  methods: {
    deleteUser(index) {
      console.log(this.tableData[index]);
      this.deleteDialog.user = this.tableData[index].name;
      this.deleteDialog.deletDialogVisible = true;
      this.deleteDialog.index = index;

    },
    addUserButtonClick() {
      this.addUserDialogVisible = true;
    },
    confirmDeleteUser() {
      const adminUuid = this.$cookies.get("adminUuid")

      Request.post("/api/deleteUser", {
        "adminUserID": adminUuid,
        "userID": this.tableData[this.deleteDialog.index].userId,

      }).then((res) => {

        console.log(res);
        if (res.data.resCode != 0) {
          this.$message.error('删除用户出错:' + res.data.message);

        } else {
          this.deleteDialog.deletDialogVisible = false;
          this.$router.push("/refresh");
        }
      });
    },

    clickAddUsers() {
      console.log(this.authorityValue)
      const adminUuid = this.$cookies.get("adminUuid")
      const authority = CommonConstants.authorityMap.get(this.authorityValue);
      // this.addUserDialogVisible = false;
      Request.post("/api/addUser", {
        "userAccount": this.form.name,
        "userPassword": this.form.password,
        "adminUserID": adminUuid,
        "userAuthority": authority
      }).then((res) => {

        console.log(res);
        if (res.data.resCode != 0) {
          this.$message.error('添加用户名错误:' + res.data.message);

        } else {
          this.addUserDialogVisible = false;
          this.form.name = "";
          this.form.password = "";
          this.$router.push("/refresh")
        }
      });
    }
  }
}
</script>

<style scoped>
.loginCard {
  margin-top: 50px;
  margin-left: 50px;
}

.loginCard /deep/ .el-table td, .el-table th {
  text-align: center;
}

.deleText {
  font-size: 20px;
}

.dialog-footer {
  text-align: center;
  margin-top: 20px;
}
</style>



